$prefix: ".wc-booster";
#{$prefix}-product-companion.wc-booster-layout-column {
	padding: 9px 10px;
}
#{$prefix}-product-companion.wc-booster-layout-row {
	padding: 8px 13px 5px;
	gap: 15px;
	.wc-booster-wishlist-button-wrapper {

		.wc-booster-wishlist-button {
			padding: 0px;
		}
		.wishlist-text {
			top: 56px;
			left: -50px;
			right: 0;
			min-width: 120px;
			transform: translateY(10px);
			span {
				&:before {
					clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
					top: -10px;
					left: 0;
					right: 0;
					margin: 0 auto;
					transform: unset;
				}
			}
		}
		&:hover {
			.wishlist-text {
				transform: translateY(-20px);
			}
		}
	}

	.wc-booster-quick-view .wc-booster-quick-view-button {
		.quick-view-text {
			top: 56px;
			left: -36px;
			right: 0;
			min-width: 120px;
			transform: translateY(10px);
			span {
				&:before {
					clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
					top: -10px;
					left: 0;
					right: 0;
					margin: 0 auto;
					transform: unset;
				}
			}
		}
		&:hover {
			.quick-view-text {
				transform: translateY(-20px);
			}
		}
	}

	.product-companion-cart {
		.cart-text {
			top: 56px;
			left: -35px;
			right: 0;
			min-width: 120px;
			transform: translateY(10px);
			span {
				&:after {
					clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
					top: -10px;
					left: 0;
					right: 0;
					margin: 0 auto;
					transform: unset;
				}
			}
		}
		&:hover {
			.cart-text {
				transform: translateY(-20px);
			}
		}
	}
}
#{$prefix}-product-companion {
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	position: absolute;
	z-index: 9999;
	gap: 5px;

	#{$prefix}-wishlist-button-wrapper {
		position: relative !important;
		top: 0 !important;
		left: 0 !important;
		.wc-booster-wishlist-button {
			padding: 0px;
		}
		.wishlist-text {
			top: -2px;
			right: -125px;
		}

		i {
			font-size: 15px;
			padding: 0 !important;
		}
	}
	#{$prefix}-quick-view {
		position: relative !important;
		top: 0 !important;
		left: 0 !important;
		padding: 0 !important;
		.quick-view-text {
			top: -2px;
			right: -105px;
		}

		i {
			font-size: 16px;
		}

		.quick-view-text-left {
			right: unset;
			left: -90px;
			top: -5px;
			transform: translateX(-20px);
			span {
				&:before {
					left: unset;
					right: -16px;
					top: 50%;
					clip-path: polygon(100% 50%, 0 100%, 0 0);
					transform: translate(-50%, -50%);
				}
			}
		}
		&.loading{
			gap: 0;

			&:after {
				position: relative;
				top: 0;
				right: 0;
				width: 13px;
				height: 13px;
				margin: 4px 0;
			}
		}
	}
	.product-companion-cart {
		position: relative !important;
		top: 0 !important;
		left: 0 !important;
		.cart-text {
			position: absolute;
			top: 0px;
			right: -105px;
			visibility: hidden;
			opacity: 0;
			transition: all 0.5s ease-in-out;
			transform: translateX(20px);
			span {
				padding: 5px 10px;
				background-color: rgba(42, 42, 42, 0.4862745098);
				color: #fff;
				position: relative;
				font-size: 14px;
				cursor: pointer;
				&:after {
					content: "";
					position: absolute;
					left: -5px;
					top: 50%;
					width: 11px;
					height: 11px;
					background-color: rgba(42, 42, 42, 0.4862745098);
					clip-path: polygon(0 50%, 100% 100%, 100% 0);
					transform: translate(-50%, -50%);
				}
			}
		}
		.add_to_cart_button {
			padding: unset !important;
			i {
				font-size: 15px;
			}
			&.loading{

				&:after{
					content: '';
					display: block;
					border: 2px solid #ffffff;
					border-radius: 50%;
					border-top: 2px solid #704747;
					width: 13px;
					height: 13px;
					-webkit-animation: spin 2s linear infinite;
					animation: spin 2s linear infinite;
					margin: 6px 0;
				}
				@keyframes spin {
					0% { transform: rotate(0deg); }
					100% { transform: rotate(360deg); }
				}

				i{
					display: none;
				}
			}

			&.added {
				.fa-shopping-cart{
					color: green !important;
				}
			}	
		}
		&:hover {
			.cart-text {
				visibility: visible;
				opacity: 1;
				transform: translateX(0);
			}
		}
		.added_to_cart {
			display: none;
		}
	}
}

